@import url("https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=PT+Serif:wght@400;700&family=Playfair+Display&family=Poppins:wght@400;500;600&display=swap");

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root {
    --color-bg-dark: #aa5377;
    --color-bg-black: #3b3759;
    --color-white: #ffffff;
    --color-primary: #cf8da7;
    --color-primary-variant: rgba(12, 29, 40, 0.4); 
    --color-danger: rgb(84, 235, 249);
}

body {
    font-family: 'PT Serif', serif;
    display: grid;
    place-items: center;
    color: var(--color-primary);
    background: #ccc;
    line-height: 1.5;
}

.btn {
    background: var(--color-bg-dark);
    border-radius: 0.8rem;
    padding: 0.8rem;
    font-size: 1.6rem;
    color: var(--color-primary);
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
    transition: all 300ms ease;
}

.btn.lg {
    padding: 0.8rem 1.5rem;
    font-size: 1.2rem;
    box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, 0.4);
}

.btn.danger {
    background: var(--color-bg-dark);
    color: var(--color-white);
}

.btn.primary {
  background: var(--color-primary);
  color: var(--color-white);
}

.btn:hover {
    cursor: pointer;
    box-shadow: none;
}

#list__lang{
    display: flex;
    list-style-type:none;
    gap: 0.5rem;
    justify-content: center;
  
}
.lang {
  background: var(--color-bg-dark);
  color: var(--color-white);
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  transition: all 300ms ease;
  border-radius: 0.8rem;
  padding: 0.3rem;
  font-size: 0.5rem;
}

#app {
    background: var(--color-bg-black);
    height: 48rem;
    width: 24rem;
    padding: 2rem 1.6rem;
    overflow-y: scroll;
    position: relative;
}

::-webkit-scrollbar {
    display: none;
}

h2,  h4 {
    font-weight: 500;
}

h2 {
    font-size: 2rem;
}

/* NOTES PAGE */
.notes__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1.6rem 1.5rem;
  background: var(--color-bg-black);
  z-index: 9;
}


.notes__header input {
  padding: 0.7rem 1rem;
  background: transparent;
  border: 1px solid var(--color-bg-dark);
  border-radius: 0.6rem;
  color: var(--color-white);
  font-size: 1.1rem;
  width: 100%;
  margin-right: 1.5rem;
}

.notes__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.2rem;
}

.empty__notes {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.note {
  background: var(--color-primary);
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  cursor: pointer;
  transition: all 300ms ease;
  color: var(--color-white);
  border: white solid 1px;
  border-radius: 1rem;

}

.note:hover {
  color: #0A192F;
  opacity: 0.8;
}

.note:nth-child(3) {
  grid-column: 1/3;
}

.note:nth-child(4) {
  grid-row: 3/5;
}

.note:nth-child(7) {
  grid-column: 1/3;
}

.note p {
  font-size: 0.6rem;
  opacity: 0.85;
}

.btn.add__btn {
  position: fixed;
  bottom: 4rem;
  right: 9rem;
  padding: 1rem;
}


/* CREATE NOTE & EDIT NOTE PAGE */
.create-note__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.create-note__form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
  
}

.create-note__form input, .create-note__form textarea {
  width: 100%;
  padding: 0.5rem 1rem;
  background: var(--color-primary-variant);
  border-radius: 1rem;
  font-size: 1rem;
  box-shadow: 0 1rem 1.5rem rgba(2, 0, 0, 0.3);
  color: var(--color-primary);
}
.create-note__form input {
  font-size: 2rem;
  color: var(--color-primary);
}


/* MEDIA QUERIES FOR PHONES */
@media screen and (max-width: 600px) {
  #app {
      width: 100vw;
      height: 100vh;
      padding: 2rem 1rem;
  }

  .notes__header {
      width: 100%;
      padding: 2rem 1rem;
      position: absolute;
      top: 1;
      left: 0;
  }

  .notes__container {
      margin-top: 10rem;
      gap: 1rem;
  }

  .btn.add__btn {
      padding-top: 2rem;
      padding: 1.5rem;
      font-size: 1rem;
      bottom: 6%;
      right: 2rem;
    
  }
  #list__lang{
    display: flex;
    list-style-type:none;
    gap: 0.5rem;
    justify-content: center;
  
}
.lang {
  background: var(--color-primary);
  color: var(--color-white);
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  transition: all 300ms ease;
  border-radius: 0.8rem;
  padding: 0.3rem;
  font-size: 0.5rem;
}
}